import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router'
import { Ejercicio } from 'src/app/members/student/Others/Models/ejercicio.model';
import {EjerciciosService} from '../../../Others/Services/ejercicios/ejercicios.service';
import { EnunciadoComponent } from '../../../Others/Map/Popups/enunciado/enunciado.component';
import { PopoverController } from '@ionic/angular';
import { HelpComponent } from '../../../Others/Map/Popups/help/help.component';
import { HintsComponent } from '../../../Others/Map/Popups/hints/hints.component';


@Component({
  selector: 'app-ws-mat',
  templateUrl: './ws-mat.page.html',
  styleUrls: ['./ws-mat.page.scss'],
})
export class WSMatPage implements OnInit {
  hintsClick=0;
  ejercicio: Ejercicio;
  hola: Promise<void>;
  bombilla:string;
  public singleBlockly = false;
  public dualBlockly = false;
  
  constructor( private router: Router,private ejerciosService: EjerciciosService, public popoverController: PopoverController) { }

  ngOnInit() {
    const hola= this.router.url;
    this.ejercicio = this.ejerciosService.getEjercicio(hola);

    //Store the actual exercise 
    var exerciseDoing= this.ejercicio.subtitle
    localStorage.setItem('exerciseDoing',exerciseDoing)

    //Known if the exercise is comparative or not.
    var double=this.ejercicio.comp
    console.log(double)

    if(double==true){
      this.dualBlockly=true
      this.singleBlockly=false
    }
    else if(double==false){
      this.singleBlockly=true
      this.dualBlockly=false
    }

    
    //Only needed for the DEMO exercise
    //this.hola=this.presentinitHelp()
    this.bombilla="bulb-outline"

  }
  //Delete the option of answer and the lists of places once you go out of an exercise.
  killAllowAnswer(){
    localStorage.removeItem('allowAnswer');
    localStorage.removeItem('listReg');
    localStorage.removeItem('listLatinReg');
    localStorage.removeItem('listProv');
    localStorage.removeItem('listLatinProv');
    localStorage.removeItem('listRegDual');
    localStorage.removeItem('listLatinRegDual');
    localStorage.removeItem('listProvDual');
    localStorage.removeItem('listLatinProvDual');
    localStorage.removeItem('listRegTeacher');
    localStorage.removeItem('listLatinRegTeacher');
    
  }
  
  async presentEnunciado(eve) {
    const popover = await this.popoverController.create({
      component: EnunciadoComponent,
      componentProps:{

      },
      cssClass: 'popOver',
      event: eve,
      translucent: true
    });
    return await popover.present();

  }
  async presentHints(eve) {
    const popover = await this.popoverController.create({
      component: HintsComponent,
      componentProps:{

      },
      cssClass: 'popOver',
      event: eve,
      translucent: true
    });
    this.bombilla="bulb"
    this.hintsClick++;
    var clickString= String(this.hintsClick)
    localStorage.setItem(clickString,'hintsClick')
    console.log(clickString)
    return await popover.present(),this.bombilla;

  }

  async presentHelp(eve) {
    const popover = await this.popoverController.create({
      component: HelpComponent,
      componentProps:{

      },
      cssClass: 'popOver',
      event: eve,
      translucent: true
    });
    return await popover.present();
  }
  
  async presentinitHelp() {
    const popover = await this.popoverController.create({
      component: HelpComponent,
      componentProps:{

      },
      cssClass: 'popOverInit',
      translucent: true
    });
    return await popover.present();
  }
}

